	margin: 0 auto 40px auto;
	text-align: center;
	color: #505458;
}
.login_remember {
	margin: 0 0 35px 0;
	text-align: left;
}

.home_container {
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
}
.el-header {
	background-color: #24292E;
	color: #333;
