}
.el-aside {
	background-color: #ECECEC;
}
.el-main {
	background-color: #fff;
	color: #000;
	text-align: center;
}
.home_title {
	color: #fff;
	font-size: 22px;
	display: inline;
}
.home_userinfo {
	color: #fff;
	cursor: pointer;
}
.home_userinfoContainer {
	display: inline;
	margin-right: 20px;
}

