.el-progress.is-exception .el-progress__text {
    color: #F56C6C; }
.el-progress-bar {
  padding-right: 50px;
  display: inline-block;
  vertical-align: middle;
  width: 100%;
  margin-right: -55px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }
.el-progress-bar__outer {
    height: 6px;
    border-radius: 100px;
    background-color: #EBEEF5;
